body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
    position: relative;
    min-height: 100%;
}

html, body {
    margin: 0;
    padding: 0;
    background: #FFFFFF;
}

.logo {
    margin-left: 10px;
    margin-top: 5px;
    float: left;
}

.footer {
    height: 25%;
    background: #C5D9D9;
}

.footer-contacts {
    position: absolute;
    margin-left: 10px;
    bottom: 0;
    margin-bottom: 10px;
}

.footer-contacts span {
    white-space: pre-line;
}

.content {
    position: absolute;
    width: 100%;
    height: 70%;
}

.container-custom {
    margin-top: 20px;
}

.my-navbar {
    background-color: #C5D9D9;
}

.blog-img {
    max-width: 50%;
}

.row {
    --bs-gutter-x: 0rem;
    --bs-gutter-y: 0;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(-1 * var(--bs-gutter-y));
    margin-right: calc(-.5 * var(--bs-gutter-x));
    margin-left: calc(-.5 * var(--bs-gutter-x));
}

.container-blog {
    max-width: 50%;
}

.button-tar {
    text-align: right;
    margin-bottom: 10px;
}

